.prioritySortButton {
    position: inherit;
    width: 30px;
    height: 30px;
    margin-top: -2px;
    border-radius: 100%;
    border: none;
    outline: none;
    background: #e9e9e9;
    opacity: .5;
    margin-left: 1vw;}

.prioritySortButton:focus{
    opacity: 1;
}

.prioritySortButton:hover{
    opacity: 1;
}

.TaskPriorityButton{
    position: inherit;
    width: 40px;
    height: 40px;
    margin-top: 0px;
    border-radius: 20%;
    border: none;
    outline: none;
    background: #fabd85;
    margin-right: -2.5vw;
    opacity: .8;
}

.TaskPriorityButton:focus{
    opacity: 1;
}

.priority-container {
    position: fixed;
    display: flex;
    flex-direction: column;
    width: 400px;
    top: calc(100vh - 400px);
    left: calc(100vw - 450px);
    background: #e1e1e1;
    padding: 20px;
    z-index: 20;
    border-radius: 20px;
    gap: 5px;
    align-items: center;
}

.priority-container-title{
    font-size: 2em;
    color: #7a7a7a;
    outline: none;
    border: none;
    background: transparent;
    border-radius: 10px;
    font-family: "Roboto";
    margin-bottom: 10px;
    text-align: left;
    margin-left: 20px;
    width: 350px;
    padding: 5px;
    height: 50px;
    top: 50%;

}

.priorityIcon{
    margin-right: 5px   ;
}


@media screen and (max-width: 900px) and (max-height: 500px) {
    .priority-container {
        width: 400px;
        top: calc(100vh - 350px);
        left: calc(100vw - 450px);
        background: #e1e1e1;
        padding: 20px;
        z-index: 20;
        border-radius: 20px;
        gap: 5px;
        align-items: center;
        font-size: .8em;
        height: 300px;
    }

}



@media screen and (max-width: 600px) and (max-height: 1000px) {

    .priority-container {
        display: flex;
        flex-direction: column;
        gap: 5px;
        align-items: center;
        width: 250px;
        left: calc(100vw - 300px);
    }

    .priority-container-title{
        font-size: 1.5em;
        padding: 5px;
        height: 50px;
        border-radius: 10px;
        font-family: "Roboto";
        text-align: left;
        margin-left: 35px;
        top: 50%;
        width: 230px;

    }


    .TaskPriorityButton{
        position: inherit;
        width: 30px;
        height: 30px;
        margin-top: 1px;
        margin-left: 15px;
        border-radius: 100%;
        border: none;
        outline: none;
        background: #fabd85;
        opacity: .6;
    }



    .prioritySortButton {
        position: relative;
        width: 30px;
        height: 30px;
        margin-top: 9px;
        border-radius: 100%;
        border: none;
        outline: none;
        background: #e9e9e9;
        opacity: .6;
    }

}